export default {
	data(){
		return {
			"navs": []
		}
	},
	onLoad() {
		this.getList();
	},
	methods:{
		jump:function(e){
			uni.navigateTo({
				url: e.url + "?title=" + e.title
			})
		},
		getList:function(){
			let that = this;
			uni.request({
				url:"/mock/mockData",
				success:function(res){
					console.log(res);
					that.navs = res.navs.model;
				}
			})
		}
	}
}
